import React, { Component } from 'react';
import './index.css';




class Sections extends Component {

    render() {
      return (
        <React.Fragment>
        <header>
        <h1>THE CODING CHRONICLES</h1>

          <nav>
            <a href="/" onClick={() => this.props.handleSelection('articles')}>Home</a>
            <a href="/entertainment">Entertainment</a>
            <a href="/sports">Sports</a>
            <a href="/editorials">Editorials</a>

            <a href ="log-in" onClick={()=> this.props.handleSelection('login')}>Log In</a>
            <a href="create-account" onClick={()=> this.props.handleSelection('register')}>Create Account</a>

        </nav>
        <div className="black"></div>
        <div className="top-divide">
        <p className="top">TOP STORIES</p>
        <p className="pick">TRENDING</p>
        </div>
        </header>

        </React.Fragment>
      )
}
}
 export default Sections




















